      <el-table-column label="30%">
        <template slot-scope="{row,$index}">
          <el-input v-model="row.personal"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="{row,$index}">
          <el-input v-model="row.real"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="{row,$index}">
          <el-input v-model="row.diff"></el-input>
        </template>
      </el-table-column>
    </el-table>
    <div class="footer">
      <el-button class="btn" type="success" @click="submit"></el-button>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'index',
    data() {
      return {
        tableData: [{
          index: 1,
          name: '',
          personal: '',
          real: '',
          diff: ''
        }, {
          index: 2,
          name: '',
          personal: '',
          real: '',
          diff: ''
        }, {
          index: 3,
          name: '',
          personal: '',
          real: '',
          diff: ''
        }, {
          index: 4,
          name: '',
          personal: '',
          real: '',
          diff: ''
        }, {
          index: 5,
          name: '',
          personal: '',
          real: '',
          diff: ''
        }, {
          index: 6,
          name: '',
          personal: '',
          real: '',
          diff: ''
        }]
      }
    },
    created() {
      this.getData()
    },
    methods: {
      submit() {
        console.log(this.tableData)
        let params = {
          uid: '',
          info: JSON.stringify(this.tableData)
        }
        this.$http.get('', {params: params})
        .then((res) => {
          if (res.code === 200) {
            alert('')
            history.go(-1)
          } else {
            alert(res.msg)
          }
        })
        .catch((err) => {
          console.log(err)
        })
      },
      getData() {
        let params = {
          uid: ''
        }
        this.$http.get('', {params: params})
        .then((res) => {
          if (res.code === 200) {
            this.tableData = res.data
          } else {
            alert(res.msg)
          }
        })
        .catch((err) => {
          console.log(err)
        })
      }
    },
    components: {
  
    }
  }
</script>

